import React, { Component } from 'react';
import {Navbar, NavbarBrand, NavbarToggler, NavItem, Collapse, Nav, Button, Modal, ModalHeader, ModalBody,
    Form, FormGroup, Input, Label} from 'reactstrap';
import { Jumbotron, Container } from 'reactstrap';
import { NavLink } from 'react-router-dom';

class Header extends Component{
	constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
    this.handleLogin = this.handleLogin.bind(this);

    this.state = {
      isOpen: false,
      isModalOpen: false,
    };
  }

  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  toggleModal(){
  	this.setState({
  		isModalOpen: !this.state.isModalOpen
  	});
  }

  handleLogin(event) {
        this.toggleModal();
        alert("Username: " + this.username.value + " Password: " + this.password.value
            + " Remember: " + this.remember.checked);
        event.preventDefault();

    }


	render() {
		return(

			<React.Fragment>
                <div>
				<Navbar dark expand="md">
		          <div className= "container">
		            <NavbarBrand className="mr-auto" href="/">Ristorante Con Fusion</NavbarBrand>

		            <NavbarToggler onClick={this.toggle} />
			          <Collapse isOpen={this.state.isOpen} navbar>
			            <Nav navbar>
			              <NavItem>
			                <NavLink className="nav-link" to="/home"><span className = "fa fa-home fa-lg"></span>Home</NavLink>
			              </NavItem>
			              <NavItem>
			                <NavLink className="nav-link" to="/menu"><span className = "fa fa-list fa-lg"></span>Menu</NavLink>
			              </NavItem>
			              <NavItem>
			                <NavLink className="nav-link" to="/aboutus"><span className = "fa fa-info fa-lg"></span>About</NavLink>
			              </NavItem>
			              <NavItem>
			                <NavLink className="nav-link" to="/contactus"><span className = "fa fa-address-card fa-lg"></span>Contact</NavLink>
			              </NavItem>
			            </Nav>
			            <Nav className="ml-auto" navbar>
                                <NavItem>
                                    <Button outline onClick={this.toggleModal}><span className="fa fa-sign-in fa-lg"></span> Login</Button>
                                </NavItem>
                            </Nav>
			          </Collapse>

		          </div>
		        </Navbar>
		        </div>
	        	<Jumbotron>
	        		<div className="container">
	        		<div className="row row-header">
			        <div  className="col-12 col-sm-6">
			          <h1>Ristorante con Fusion</h1>
                       <p>We take inspiration from the World's best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
			        </div>
			        </div>
			        </div>
			    </Jumbotron>

			    <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
                    <ModalBody>
                    <Form onSubmit={this.handleLogin}>
                            <FormGroup>
                                <Label htmlFor="username">Username</Label>
                                <Input type="text" id="username" name="username"
                                    innerRef={(input) => this.username = input} />
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="password">Password</Label>
                                <Input type="password" id="password" name="password"
                                    innerRef={(input) => this.password = input}  />
                            </FormGroup>
                            <FormGroup check>
                                <Label check>
                                    <Input type="checkbox" name="remember"
                                    innerRef={(input) => this.remember = input}  />
                                    Remember me
                                </Label>
                            </FormGroup>
                            <Button type="submit" value="submit" color="primary">Login</Button>
                        </Form>
                    </ModalBody>
                </Modal>
                
	        </React.Fragment> 


		)
	}
}

export default Header;